import { Router } from '../router/Router.js';
import { DashboardView } from '../views/DashboardView.js';
import { NavBar } from '../components/NavBar.js';
import { ViewId } from '../router/view-ids.js';
import { selectElement } from '../utils/dom.js';

/**
 * Initializes the Router, registers Views, and sets up the Navigation Bar.
 */
export function setupRouter(): Router {
    const router = new Router(selectElement('mainContent'));

    const dashboardView = new DashboardView();
    router.registerView(dashboardView);

    // Register Lazy Views
    router.registerLazyView(ViewId.History, () => import('../views/HistoryView.js').then((m) => new m.HistoryView()));
    router.registerLazyView(ViewId.Workouts, () =>
        import('../views/WorkoutsView.js').then((m) => new m.WorkoutsView())
    );
    router.registerLazyView(ViewId.Fitness, () => import('../views/FitnessView.js').then((m) => new m.FitnessView()));
    router.registerLazyView(ViewId.Settings, () =>
        import('../views/SettingsView.js').then((m) => new m.SettingsView())
    );
    router.registerLazyView(ViewId.Plans, () => import('../views/PlansView.js').then((m) => new m.PlansView()));
    router.registerLazyView(ViewId.Debug, () => import('../views/DebugView.js').then((m) => new m.DebugView()));
    router.registerLazyView(ViewId.Help, () => import('../views/HelpView.js').then((m) => new m.HelpView()));

    router.addRoute('/', ViewId.Dashboard);
    router.addRoute('/history', ViewId.History);
    router.addRoute('/workouts', ViewId.Workouts);
    router.addRoute('/fitness', ViewId.Fitness);
    router.addRoute('/plans', ViewId.Plans);
    router.addRoute('/settings', ViewId.Settings);
    router.addRoute('/debug', ViewId.Debug);
    router.addRoute('/help', ViewId.Help);

    // Initialize Navigation Bar
    new NavBar(router);

    // Wire up Sidebar Settings Button (for mobile/hamburger menu)
    const settingsButton = document.getElementById('settingsButton');
    if (settingsButton) {
        settingsButton.addEventListener('click', () => {
            router.navigate('/settings');
            // Close the details menu if open
            const details = document.querySelector('nav details') as HTMLDetailsElement;
            if (details) {
                details.open = false;
            }
        });
    }

    // Wire up Sidebar Workouts Button
    const workoutsButton = document.getElementById('workoutsButton');
    if (workoutsButton) {
        workoutsButton.addEventListener('click', () => {
            router.navigate('/workouts');
            const details = document.querySelector('nav details') as HTMLDetailsElement;
            if (details) {
                details.open = false;
            }
        });
    }

    // Wire up Sidebar Plans Button
    const plansButton = document.getElementById('plansButton');
    if (plansButton) {
        plansButton.addEventListener('click', () => {
            router.navigate('/plans');
            const details = document.querySelector('nav details') as HTMLDetailsElement;
            if (details) {
                details.open = false;
            }
        });
    }

    // Wire up Sidebar Help Button
    const aboutButton = document.getElementById('aboutButton');
    if (aboutButton) {
        aboutButton.addEventListener('click', () => {
            router.navigate('/help');
            const details = document.querySelector('nav details') as HTMLDetailsElement;
            if (details) {
                details.open = false;
            }
        });
    }

    return router;
}
