import { goalsStore } from '../state/GoalsStore.js';
import { showModal } from './modal.js';
import { showNotification } from './notifications.js';

let initialized = false;

export function initGoalsView() {
    if (initialized) return;

    const addBtn = document.getElementById('addGoalBtn');
    addBtn?.addEventListener('click', showAddGoalModal);

    goalsStore.subscribe(() => {
        refreshGoalsView();
    });

    initialized = true;
}

export async function refreshGoalsView() {
    const list = document.getElementById('goalsList');
    if (!list) return;

    const goals = await goalsStore.getGoals();

    if (goals.length === 0) {
        list.innerHTML = `
            <div style="grid-column: 1/-1; text-align: center; padding: 40px; color: var(--color-text-secondary); background: var(--card-bg); border-radius: 8px;">
                <p style="margin-bottom: 12px; font-size: 1.2em;">No goals set yet</p>
                <p>Create a goal to track your progress!</p>
            </div>
        `;
        return;
    }

    list.innerHTML = '<div style="grid-column: 1/-1; text-align: center; padding: 20px;">Loading progress...</div>';

    try {
        // Calculate progress for all
        const progressPromises = goals.map((g) => goalsStore.getProgress(g));
        const results = await Promise.all(progressPromises);

        list.innerHTML = results
            .map((p, index) => {
                const goal = goals[index];
                const statusColor = p.isCompleted ? 'var(--color-success)' : 'var(--color-accent)';
                const statusText = p.isCompleted ? 'Completed' : 'In Progress';

                return `
                <div class="goal-card" style="
                    background: var(--card-bg);
                    padding: 16px;
                    border-radius: 8px;
                    border: 1px solid var(--color-border);
                    position: relative;
                ">
                    <div style="display: flex; justify-content: space-between; align-items: flex-start; margin-bottom: 12px;">
                        <div>
                            <h4 style="margin: 0; font-size: 1.1em; color: var(--color-text-primary);">${goal.title}</h4>
                            <div style="font-size: 0.85em; color: var(--color-text-secondary); margin-top: 4px;">
                                ${p.periodLabel}
                            </div>
                        </div>
                        <button class="goal-delete-btn" data-id="${goal.id}" aria-label="Delete Goal" style="
                            background: none; border: none; font-size: 1.2em; cursor: pointer; opacity: 0.5; color: var(--color-text-secondary);
                        ">&times;</button>
                    </div>

                    <div style="display: flex; justify-content: space-between; align-items: flex-end; margin-bottom: 8px;">
                        <span style="font-size: 1.5em; font-weight: bold; color: var(--color-text-primary);">${p.formattedCurrent}</span>
                        <span style="color: var(--color-text-secondary); padding-bottom: 4px;">of ${p.formattedTarget}</span>
                    </div>

                    <div style="
                        height: 8px;
                        background: rgba(128,128,128,0.2);
                        border-radius: 4px;
                        overflow: hidden;
                    ">
                        <div style="
                            width: ${p.percent}%;
                            height: 100%;
                            background: ${statusColor};
                            transition: width 0.5s ease;
                        "></div>
                    </div>

                    ${
                        p.isCompleted
                            ? `
                        <div style="margin-top: 12px; font-size: 0.9em; color: var(--color-success); display: flex; align-items: center; gap: 6px;">
                            <span></span> Goal Completed!
                        </div>
                    `
                            : `
                        <div style="margin-top: 12px; font-size: 0.85em; color: var(--color-text-secondary); display: flex; justify-content: space-between;">
                            <span>${statusText}</span>
                            <span>${p.remaining > 0 ? (goal.type === 'distance' ? `${p.remaining.toFixed(1)} km left` : '') : ''}</span>
                        </div>
                    `
                    }
                </div>
            `;
            })
            .join('');

        // Attach delete handlers
        list.querySelectorAll('.goal-delete-btn').forEach((btn) => {
            btn.addEventListener('click', (e) => {
                e.stopPropagation();
                const id = (e.currentTarget as HTMLElement).dataset.id;
                if (id && confirm('Delete this goal?')) {
                    goalsStore.deleteGoal(id);
                }
            });
        });
    } catch (e) {
        console.error('Error rendering goals:', e);
        list.innerHTML = `<div style="grid-column: 1/-1; color: red;">Error loading goals</div>`;
    }
}

function showAddGoalModal() {
    const content = document.createElement('div');
    content.innerHTML = `
        <div class="form-group" style="margin-bottom: 16px;">
            <label style="display: block; margin-bottom: 6px; font-weight: 500;">Goal Title</label>
            <input type="text" id="goalTitle" placeholder="e.g. Weekly Distance" style="width: 100%; padding: 8px; border: 1px solid var(--color-border); border-radius: 4px; background: var(--input-bg); color: var(--color-text-primary);">
        </div>
        
        <div style="display: grid; grid-template-columns: 1fr 1fr; gap: 12px; margin-bottom: 16px;">
            <div class="form-group">
                <label style="display: block; margin-bottom: 6px; font-weight: 500;">Type</label>
                <select id="goalType" style="width: 100%; padding: 8px; border: 1px solid var(--color-border); border-radius: 4px; background: var(--input-bg); color: var(--color-text-primary);">
                    <option value="distance">Distance (km)</option>
                    <option value="duration">Duration (hours)</option>
                    <option value="tss">TSS</option>
                    <option value="count">Ride Count</option>
                </select>
            </div>
            <div class="form-group">
                <label style="display: block; margin-bottom: 6px; font-weight: 500;">Period</label>
                <select id="goalPeriod" style="width: 100%; padding: 8px; border: 1px solid var(--color-border); border-radius: 4px; background: var(--input-bg); color: var(--color-text-primary);">
                    <option value="weekly">Weekly</option>
                    <option value="monthly">Monthly</option>
                </select>
            </div>
        </div>

        <div class="form-group" style="margin-bottom: 16px;">
            <label style="display: block; margin-bottom: 6px; font-weight: 500;">Target Value</label>
            <input type="number" id="goalTarget" placeholder="e.g. 100" style="width: 100%; padding: 8px; border: 1px solid var(--color-border); border-radius: 4px; background: var(--input-bg); color: var(--color-text-primary);">
            <div id="goalTargetHint" style="font-size: 0.85em; color: var(--color-text-secondary); margin-top: 4px;">Target in km</div>
        </div>
    `;

    // Hint update logic
    const updateHint = () => {
        const typeEl = content.querySelector('#goalType') as HTMLSelectElement;
        const hint = content.querySelector('#goalTargetHint');
        const type = typeEl?.value;
        if (!hint || !type) return;

        switch (type) {
            case 'distance':
                hint.textContent = 'Target in km';
                break;
            case 'duration':
                hint.textContent = 'Target in hours';
                break;
            case 'tss':
                hint.textContent = 'Training Stress Score points';
                break;
            case 'count':
                hint.textContent = 'Number of rides';
                break;
        }
    };
    content.querySelector('#goalType')?.addEventListener('change', updateHint);

    let closeModal: () => void;

    const save = async () => {
        const titleEl = content.querySelector('#goalTitle') as HTMLInputElement;
        const typeEl = content.querySelector('#goalType') as HTMLSelectElement;
        const periodEl = content.querySelector('#goalPeriod') as HTMLSelectElement;
        const targetEl = content.querySelector('#goalTarget') as HTMLInputElement;

        const title = titleEl.value;
        const type = typeEl.value as any;
        const period = periodEl.value as any;
        const target = parseFloat(targetEl.value);

        if (!title || !target) {
            alert('Please fill in all fields');
            return;
        }

        try {
            await goalsStore.addGoal({
                title,
                type,
                period,
                target,
            });

            showNotification('Goal created!', 'success');
            if (closeModal) closeModal();
        } catch (e) {
            console.error(e);
            alert('Failed to save goal');
        }
    };

    closeModal = showModal({
        title: 'Add New Goal',
        content,
        buttons: [
            {
                text: 'Cancel',
                variant: 'secondary',
                onClick: () => {
                    if (closeModal) closeModal();
                },
            },
            { text: 'Save Goal', variant: 'primary', onClick: save },
        ],
    });
}
